@import "../../global.scss";

.portfolioList {
    font-size: 14px;
    margin-right: 50px;
    padding: 7px;
    border-radius: 10px;
    cursor: pointer;

    @include mobile 
    {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &.active {
        background-color: $mainColor;
        color: white;
    }
}